<template>
  <el-row>
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="右邊" name="右"></el-tab-pane>
      <el-tab-pane label="中間" name="中"></el-tab-pane>
    </el-tabs>

    <el-button type="primary" @click="addadvertise" size="small"
      >新增</el-button
    >
    <el-table
      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%"
    >
      <el-table-column label="日期" width="120">
        <template slot-scope="scope">{{ scope.row.createtime }}</template>
      </el-table-column>
      <el-table-column prop="name" label="名稱(alt)" width="150">
        <template slot-scope="scope">
          <el-input v-model="scope.row.name" maxlength="1000"></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="address" label="連接">
        <template slot-scope="scope">
          <el-input v-model="scope.row.url" maxlength="1000"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="圖片">
        <template slot-scope="scope">
          <el-link :href="'/topic/file/' + scope.row.img" target="_blank">{{
            scope.row.img
          }}</el-link>
        </template>
      </el-table-column>
      <el-table-column label="上傳" width="75">
        <template slot-scope="scope">
          <el-upload
            :show-file-list="false"
            :on-success="
              (response, file, fileList) =>
                handleChange(response, file, fileList, scope.$index)
            "
            class="upload-demo"
            action="/topic/backstage/upfile"
          >
            <el-button size="mini" type="primary"> 上傳 </el-button>
          </el-upload>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="150">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">
            修改
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
            >删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-row>
</template>

<script>
import $ from "jquery";
export default {
  data() {
    return {
      fileList: [],
      tableData: [],
      multipleSelection: [],
      activeName: "右",
    };
  },
  created() {
    $.ajax({
      url: "/topic/backstage/advertiseinit?location=右",
      type: "get",
      async: false, //同步請求
      cache: false, //不快取頁面
      success: (response) => {
        this.tableData = response;
      },
      error: function (returndata) {
        console.log(returndata);
      },
    });
  },

  methods: {
    //切換位置
    handleClick() {
      console.log(this.activeName);
      $.ajax({
        url: "/topic/backstage/advertiseinit?location=" + this.activeName,
        type: "get",
        async: false, //同步請求
        cache: false, //不快取頁面
        success: (response) => {
          this.tableData = response;
        },
        error: function (returndata) {
          console.log(returndata);
        },
      });
    },
    //上傳圖片
    handleChange(response, file, fileList, index) {
      const row = this.tableData[index];
      row.location = this.activeName;
      row.img = response;
      $.ajax({
        url: "/topic/backstage/addadvertise",
        type: "POST",
        data: row,
        async: false, //同步請求
        cache: false, //不快取頁面
        success: (response) => {
          this.tableData = response;
          console.log(response);
        },
        error: function (returndata) {
          console.log(returndata);
        },
      });
    },
    //添加新一行
    addadvertise() {
      this.tableData.push({});
    },
    //修改資料
    handleEdit(index, row) {
      console.log(this.activeName, index, row);
      row.location = this.activeName;
      this.$confirm("修改確認?", "提示", {
        confirmButtonText: "確定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {          
          $.ajax({
            url: "/topic/backstage/addadvertise",
            type: "POST",
            data: row,
            async: false, //同步請求
            cache: false, //不快取頁面
            success: (response) => {
              this.tableData = response;

              this.$message({
                type: "success",
                message: "修改成功!",
              });
            },
            error: function (returndata) {
              console.log(returndata);
            },
          });
        })
        .catch(() => {});
    },
    //刪除資料
    handleDelete(index, row) {
      this.$confirm("修改刪除?", "警告", {
        confirmButtonText: "確定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          $.ajax({
            url: "/topic/backstage/deladvertise",
            type: "POST",
            data: row,
            async: false, //同步請求
            cache: false, //不快取頁面
            success: (response) => {
              this.tableData = response;
              this.$message({
                type: "success",
                message: "修改成功!",
              });
            },
            error: function (returndata) {
              console.log(returndata);
            },
          });
        })
        .catch(() => {});
    },
  },
};
</script>

<style>
</style>